import React, { useState } from 'react';
import logo from '../images/cv.png';

function Header() {
    const [show, setShow] = useState(false);

    return (
        <>
            <header>
                <div class={`bg-dark collapse  ${show ? 'show' : ''}`} id="navbarHeader" onClick={() => setShow(!show)}>
                    <div class="container">
                        <div class="row">
                            <div class="col-sm-8 col-md-7 py-4">
                                <h4 class="text-white">Hruday Ranjan</h4>
                                <p class="text-muted">
                                    Looking for an organization where I can enhance my knowledge and my past experiences. I am committed to learning any new
                                    skills on my own to succeed.
                                </p>
                            </div>
                            <div class="col-sm-4 offset-md-1 py-4">
                                <h4 class="text-white">Contact With Me</h4>
                                <ul class="list-unstyled">
                                    <li>
                                        <a href="https://twitter.com/Er_hruday" style={{ textDecoration: 'none' }} target="_blank" class="text-white">
                                            Follow on Twitter
                                        </a>
                                    </li>
                                    <li>
                                        <a
                                            href="https://www.linkedin.com/in/hrudayranjan/"
                                            style={{ textDecoration: 'none' }}
                                            target="_blank"
                                            class="text-white"
                                        >
                                            Follow on LinkedIn
                                        </a>
                                    </li>
                                    <li>
                                        <p class="text-white">hrudayranjan8@gmail.com</p>
                                    </li>
                                </ul>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="navbar navbar-dark bg-dark shadow-sm">
                    <div class="container">
                        <a href="#" class="navbar-brand d-flex align-items-center">
                            {/* <svg
                                xmlns="http://www.w3.org/2000/svg"
                                width="20"
                                height="20"
                                fill="none"
                                stroke="currentColor"
                                stroke-linecap="round"
                                stroke-linejoin="round"
                                stroke-width="2"
                                aria-hidden="true"
                                class="me-2"
                                viewBox="0 0 24 24"
                            >
                                <path d="M23 19a2 2 0 0 1-2 2H3a2 2 0 0 1-2-2V8a2 2 0 0 1 2-2h4l2-3h6l2 3h4a2 2 0 0 1 2 2z" />
                                <circle cx="12" cy="13" r="4" />
                            </svg> */}
                            <img src={logo} alt="cv" className="m-2" />
                            <strong>Hruday Ranjan</strong>
                        </a>
                        <button
                            class="navbar-toggler"
                            type="button"
                            data-bs-toggle="collapse"
                            data-bs-target="#navbarHeader"
                            aria-controls="navbarHeader"
                            aria-expanded="false"
                            aria-label="Toggle navigation"
                        >
                            <span class="navbar-toggler-icon"></span>
                        </button>
                    </div>
                </div>
            </header>
        </>
    );
}

export default Header;
